// src/pages/HomePage.js
import React from "react";
import { Link } from "react-router-dom";
import "./HomePage.css";
import Dashboard from "./Dashboard";

const HomePage = () => {
  return (
    <div className="home-container">
      <header className="home-header">
        <h1>Expense Manager</h1>
        <p>Track your expenses effortlessly and take control of your finances.</p>
      </header>

      <section className="home-content">
        <div className="home-card">
          <h2>Track Your Spending</h2>
          <p>Record daily expenses and categorize them for better insights.</p>
        </div>

        <div className="home-card">
          <h2>Analyze Your Budget</h2>
          <p>View detailed charts and reports to make smarter financial decisions.</p>
        </div>

        <div className="home-card">
          <h2>Stay Organized</h2>
          <p>All your financial data in one secure place, accessible anytime.</p>
        </div>
      </section>

      <div className="home-btn-container">
        <Link to="/Dashboard" className="home-btn">
          Get Started
        </Link>
      </div>
    </div>
  );
};

export default HomePage;
